import Asset from "utils/asset-loader"; 

import { useState } from "react";
import AvailableNetworksSelection from "./AvailableNetworksSelection";
import { PhoneCardReceiveModal } from "./PhoneCardReceiveModal";
import { getPhoneCard } from "utils/api-helper";
import StyleLoader from "utils/style-loader";

const PhoneCardWithoutValue = ({ gift, setValue }) => {
  const classes = StyleLoader.receive_reward_modal_use_style();

  const networks = gift.availableNetworks;
  return (
    networks && (
      <div className={classes.frame}>
        <div>Chọn nhà mạng và nhận ngay thẻ cào</div>
        <AvailableNetworksSelection
          networks={networks}
          onChange={(e) => {
            setValue(e.target.value);
            console.log("e.target.value", e.target.value);
          }}
        />
      </div>
    )
  );
};
export default function ReceiveRewardModal({ content, onHide }) {
  const classes = StyleLoader.receive_reward_modal_use_style();
  let text = "Chúc mừng bạn đã nhận được " + content.name;

  let category = content.category;

  let extraInfo = "";

  const [network, setNetwork] = useState(
    content.availableNetworks ? content.availableNetworks[0] : null
  );

  const [phoneCard, setPhoneCard] = useState();
  const [showPhoneCardReceiveModal, setShowPhoneCardReceiveModal] =
    useState(false);
  if (category === "GIFTCODE") {
    extraInfo = (
      <>
        <div className={classes.giftcodeValue}>{content.value}</div>
      </>
    );
  } else if (category === "CARD") {
    extraInfo = <PhoneCardWithoutValue gift={content} setValue={setNetwork} />;
  }

  const handleOnClick = async () => {
    if (category === "CARD") {
      console.log("network", network);

      const res = await getPhoneCard(network, content.giftSku);
      console.log("==== ~ handleOnClick ~ res", res);
      setPhoneCard(res.value);
      setShowPhoneCardReceiveModal(true);
    } else {
      onHide();
    }
  };

  return (
    <div className={classes.modal}>
      <div className={classes.btnExit} onClick={onHide}></div>
      <div
        className={`${classes.text} ${category === "CARD" ? classes.card : ""}`}
      >
        <div>{text}</div>
        {extraInfo}
      </div>
      <img
        src={Asset.section_2.BTN_COLLECT}
        className={classes.btnCollect}
        alt="button-collect"
        onClick={handleOnClick}
      />

      {showPhoneCardReceiveModal && phoneCard ? (
        <PhoneCardReceiveModal
          onHide={() => {
            setShowPhoneCardReceiveModal(false);
            onHide();
          }}
          card={phoneCard}
        />
      ) : null}
    </div>
  );
}
