import Leaderboard from "components/Leaderboard";
import { useEffect, useState } from "react";
import { getLeaderBoard } from "utils/api-helper";
import Asset from "utils/asset-loader";
import StyleLoader from "utils/style-loader";

function Section3() {
  const classes = StyleLoader.section_3_use_style();

  const [leaderboard, setLeaderboard] = useState();

  useEffect(() => {
    document.body.addEventListener("user_login", () => {
      console.log("user_login_event_handler");
      const getData = async () => {
        try {
          const res = await getLeaderBoard();
          setLeaderboard(res);
        } catch (error) {
          // alert(error);
        }
      };
      getData();
    });
  }, []);

  return (
    <div className={classes.section_3}>
      <img alt="frame" className={classes.frame} src={Asset.section_3.FRAME} />
      <img
        alt="section-3-title"
        className={classes.title}
        src={Asset.section_3.TITLE}
      />
      <img
        alt="overlay"
        className={classes.frameOverlay}
        src={Asset.section_3.FRAME_OVERLAY}
      />
      {leaderboard ? <Leaderboard data={leaderboard} /> : null}
      <img alt="flower" className={classes.flower} src={Asset.section_3.FLOWER}></img>
    </div>
  );
}

export default Section3;
