import { AlertModal } from "components/AlertModal";
import HistoryModal from "components/HistoryModal";
import MissionModal from "components/MissionModal";
import { MissionProcess } from "components/MissionProcess";
import { MissionReceiveModal } from "components/MissionReceiveModal";
import ReceiveRewardModal from "components/ReceiveRewardModal";
import SpinWheel from "components/SpinWheel";
import { useContext, useEffect, useState } from "react";
import {
  getMission,
  getRemainingQuota,
  getUserInformation
} from "utils/api-helper";
import Asset from "utils/asset-loader";
import Config from "utils/config";
import {
  default as Navigate,
  default as Navigator
} from "utils/navigate-helper";
import StyleLoader from "utils/style-loader";
import { GlobalStore } from "../store";

function Section_2() {
  const classes = StyleLoader.section_2_use_style();
  const [showMission, setShowMission] = useState(false);
  const [showReceiveRewardModal, setShowReceiveRewardModal] = useState(false);
  const [showHistoryModal, setshowHistoryModal] = useState(false);
  const [showMissionReceiveModal, setShowMissionReceiveModal] = useState(false);
  const [spinGift, setSpinGift] = useState();
  const [quotaInformation, setQuota] = useState(0);
  const [missionProcess, setMissionProcess] = useState();
  const [alertContent, setAlertContent] = useState();
  const [currentMilestone, setCurrentMilestone] = useState();

  const [userInformation, setUserInformation] = useState();
  const { showModal, dispatch } = useContext(GlobalStore.context);
  const resetModal = () => {
    showMission && setShowMission(false);
    showReceiveRewardModal && setShowReceiveRewardModal(false);
    showHistoryModal && setshowHistoryModal(false);
    showMissionReceiveModal && setShowMissionReceiveModal(false);
  };

  const getQuotaData = async () => {
    try {
      const res = await getRemainingQuota();
      setQuota(res);
    } catch (error) {
      console.log("==== ~ getQuotaData ~ error", error);
      // alert(error);
    }
  };

  const getUserInformationData = async () => {
    try {
      const res = await getUserInformation();
      if (!res?.isError) {
        resetModal();
        setUserInformation(res);
        if (!res.followOA) {
          setAlertContent(
            "Bạn hãy theo dõi OA Võ Lâm Truyền Kỳ và tham gia quay thưởng nhé!"
          );
        }
      } else {
        // alert(res.message);
        resetModal();
        if (res.code === -3) {
          setAlertContent(
            "Bạn hãy theo dõi OA Võ Lâm Truyền Kỳ và tham gia quay thưởng nhé!"
          );
          console.log("Error ", res.message);
        }
      }
    } catch (error) {
      resetModal();
      console.log("==== ~ getUserInformationData ~ error", error);
      // alert(error);
    }
  };

  useEffect(() => {
    dispatch({
      type: "set-overlay",
      payload:
        showHistoryModal ||
        showMission ||
        showMissionReceiveModal ||
        showReceiveRewardModal,
    });
  }, [
    dispatch,

    showHistoryModal,
    showMission,
    showMissionReceiveModal,
    showReceiveRewardModal,
  ]);

  useEffect(() => {
    if (!showModal) {
      resetModal();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [showModal]);

  const getMissionData = async () => {
    try {
      const res = await getMission();
      if (!res?.isError) setMissionProcess(res);
      else {
        // alert(res.message);
      }
    } catch (error) {
      console.log("==== ~ getMissionData ~ error", error);
      // alert(error);
    }
  };

  useEffect(() => {
    document.body.addEventListener("user_login", () => {
      console.log("user_login_event_handler");
      getUserInformationData().then(() => {
        getQuotaData();
        getMissionData();
      });
    });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const spin = () => {
    resetModal();
    getUserInformationData();
    setShowReceiveRewardModal(true);
    getQuotaData();
    checkMilestone();
  };

  useEffect(() => {
    if (userInformation && currentMilestone === undefined) {
      setCurrentMilestone(userInformation.currentMilestone);
    }
  }, [userInformation, currentMilestone]);

  useEffect(() => {
    userInformation && checkMilestone();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [userInformation]);

  const checkMilestone = () => {
    if (missionProcess && missionProcess.length)
      for (const milestone of missionProcess) {
        if (milestone.points > currentMilestone) {
          if (userInformation.userProcess >= milestone.points) {
            setCurrentMilestone(milestone);
            setShowMissionReceiveModal(true);
          }
          break;
        }
      }
  };

  const sampleMissionData = [
    {
      text: "Chia sẻ bạn bè mỗi ngày nhận 1 lượt quay.",
      action: () => {
        window.ZGS.Ui.openSocial();
        setTimeout(() => {
          document
            .querySelector(".zs-social > button > svg")
            .addEventListener("click", getQuotaData);
          document.querySelector(".zs-social-tab:nth-child(1)").click();
        }, 50);
      },
    },
    {
      text: "Mời nhóm để nhận 1 lượt quay",
      action: () => {
        window.ZGS.Ui.openSocial();
        setTimeout(() => {
          document
            .querySelector(".zs-social > button > svg")
            .addEventListener("click", getQuotaData);
          document.querySelector(".zs-social-tab:nth-child(2)").click();
        }, 50);
      },
    },
    {
      text: "Chia sẻ lên tường mỗi ngày để nhận 2 lượt quay",
      action: () => {
        window.ZGS.Ui.openSocial();
        setTimeout(() => {
          document
            .querySelector(".zs-social > button > svg")
            .addEventListener("click", getQuotaData);
          document.querySelector(".zs-social-tab:nth-child(3)").click();
        }, 50);
      },
    },
    {
      text: "Nạp mỗi ngày để nhận được 2 lượt quay",
      action: () => {
        Navigator.toPayment();
      },
    },
  ];
  return (
    <div className={classes.section_2} id="vong-quay-bang-huu">
      <img alt="title" className={classes.title} src={Asset.section_2.TITLE} />
      <img
        alt="character"
        className={classes.character}
        src={Asset.section_2.CHARACTER}
      />
      <img
        alt="button-mission"
        className={classes.btnMission}
        src={Asset.section_2.BTN_MISSION}
        onClick={() => {
          resetModal();
          setShowMission(true);
        }}
      />
      <div
        className={
          showMission || showHistoryModal || showReceiveRewardModal
            ? classes.leaving
            : classes.spinner
        }
      >
        <SpinWheel
          receive={spin}
          // gift={spinGift}
          setGift={setSpinGift}
          quota={quotaInformation.quota}
        />
      </div>
      <div className={classes.missionProcess}>
        {userInformation ? (
          <MissionProcess
            mission={missionProcess}
            userProcess={userInformation.userProcess}
          />
        ) : null}
      </div>
      {showMission ? (
        <MissionModal
          onHide={() => {
            getQuotaData();
            setShowMission(false);
          }}
          missionData={sampleMissionData}
        />
      ) : null}
      <div className={classes.banner}>
        <div className={classes.bannerText}>
          Xin chào {quotaInformation.name?.split(" ").at(-1)}, bạn có{" "}
          {quotaInformation.quota} lượt quay
        </div>
      </div>

      <div className={classes.buttonGroup}>
        <div className={classes.button}>Thể lệ</div>
        <div
          className={classes.button}
          onClick={() => {
            resetModal();

            setshowHistoryModal(true);
          }}
        >
          Lịch sử
        </div>
        <div className={classes.button} onClick={Navigate.toPlay}>
          Nhập code
        </div>
      </div>
      {showReceiveRewardModal ? (
        spinGift ? (
          <ReceiveRewardModal
            content={spinGift}
            onHide={() => {
              setShowReceiveRewardModal(false);
              setSpinGift(undefined);
            }}
          />
        ) : null
      ) : null}
      {showHistoryModal ? (
        <HistoryModal
          onHide={() => {
            setshowHistoryModal(false);
          }}
        />
      ) : null}
      {showMissionReceiveModal ? (
        <MissionReceiveModal
          onHide={() => {
            setShowMissionReceiveModal(false);
          }}
        />
      ) : null}
      {alertContent ? (
        <AlertModal
          onHide={() => {
            window.location.href = Config.OALink;
          }}
          content={alertContent}
        />
      ) : null}
    </div>
  );
}

export default Section_2;
