import { useEffect, useState } from "react";
import { getReward } from "utils/api-helper";
import Assets from "utils/asset-loader.js";
import StyleLoader from "utils/style-loader";

export default function SpinWheel({ receive, setGift, quota }) {
  const segments = [1];
  let currentSegment = "";
  let isStarted = false;
  let timerHandle = 0;
  let angleCurrent = 0;
  let angleDelta = 0;
  let maxSpeed = Math.PI / 10;
  const upTime = segments.length * 2000;
  const downTime = segments.length * 3000;
  let spinStart = 0;
  let frames = 0;

  const [image, setImage] = useState(null);
  const [canvasContext, setCanvasContext] = useState(null);

  const [onIdle, setOnIdle] = useState(true);
  useEffect(() => {
    let image = new Image();
    setImage(image);
    image.src = Assets.section_2.ITEMS;
    image.onload = () => {
      wheelInit();
    };
  }, []);
  const wheelInit = () => {
    initCanvas();
  };

  const initCanvas = () => {
    let canvas = document.getElementById("wheel-canvas");
    setCanvasContext(canvas.getContext("2d"));
  };

  useEffect(() => {
    if (!canvasContext) {
      return;
    }
    wheelDraw();
  }, [canvasContext]);

  const onFinished = () => {
    console.log("onFinished");
    setOnIdle(true);
    receive(true);
  };

  const spin = async () => {
    isStarted = true;
    if (onIdle && quota > 0) {
      if (timerHandle === 0) {
        setOnIdle(false);
        spinStart = new Date().getTime();
        maxSpeed = Math.PI / 10;
        frames = 0;
        timerHandle = window.requestAnimationFrame(onTimerTick);

        try {
          const res = await getReward();
          console.log("reward", res);
          setGift(res);
        } catch (error) {
          console.log("==== ~ spin ~ error", error);
        }
      }
    }
  };
  const onTimerTick = () => {
    frames++;
    draw();
    const duration = new Date().getTime() - spinStart;
    let progress = 0;
    let finished = false;
    if (duration < upTime) {
      progress = duration / upTime;
      angleDelta = maxSpeed * Math.sin((progress * Math.PI) / 2);
    } else {
      progress = duration / downTime;
      angleDelta = maxSpeed * Math.sin((progress * Math.PI) / 2 + Math.PI / 2);
      if (progress >= 1) finished = true;
    }
    angleCurrent += angleDelta;
    while (angleCurrent >= Math.PI * 2) angleCurrent -= Math.PI * 2;
    if (finished) {
      onFinished(currentSegment);
      timerHandle = 0;
      angleDelta = 0;
    } else {
      window.requestAnimationFrame(onTimerTick);
    }
  };

  const wheelDraw = () => {
    clear();
    drawWheel();
  };

  const draw = () => {
    clear();
    drawWheel();
  };
  const drawWheel = () => {
    const ctx = canvasContext;
    ctx.save();

    ctx.translate(150, 150);
    ctx.rotate(angleCurrent);
    ctx.drawImage(image, -150, -150, 300, 300);
    ctx.restore();
  };

  const clear = () => {
    const ctx = canvasContext;
    ctx.clearRect(0, 0, 1000, 800);
  };
  const classes = StyleLoader.spin_wheel_use_style();
  return (
    <>
      <img
        alt="spinner"
        src={Assets.section_2.SPINNER}
        className={classes.spinWheelBackground}
      />
      <div className={classes.spinWheel}>
        <canvas
          id="wheel-canvas"
          width="300"
          height="300"
          className={classes.items}
        />
        <div
          onClick={spin}
          className={onIdle ? classes.spinWheelButton : classes.spinning}
        ></div>
      </div>
    </>
  );
}
