import * as dayjs from "dayjs";
import { useEffect, useState } from "react";
import { getRewardHistory } from "utils/api-helper";
import StyleLoader from "utils/style-loader";
import { GiftcodeHistory } from "./GiftcodeHistory";

import { PhoneCardHistory } from "./PhoneCardHistory";
export default function HistoryModal({ onHide }) {
  const classes = StyleLoader.history_modal_use_style();

  const [rewardHistory, setRewardHistory] = useState();
  useEffect(() => {
    const getData = async () => {
      try {
        const res = await getRewardHistory();

        setRewardHistory(res.gifts);
      } catch (error) {
        console.log(error);
      }
    };
    getData();
  }, []);

  const renderGiftName = (gift) => {
    const receivedTime = dayjs(gift.receivedTime).format("DD/MM/YYYY HH:mm:ss");
    return <div>{receivedTime + " " + gift.name} </div>;
  };

  const renderGift = (gift, idx) => {
    let extraInfo = "";
    if (gift.category === "GIFTCODE") {
      extraInfo = <GiftcodeHistory gift={gift} />;
    } else if (gift.category === "CARD") {
      extraInfo = (
        <PhoneCardHistory gift={gift} setRewardHistory={setRewardHistory} />
      );
    }
    return (
      <div key={idx} className={classes.historyItem}>
        {renderGiftName(gift)}
        {extraInfo}
      </div>
    );
  };

  return (
    <div className={classes.modal}>
      <div className={classes.btnExit} onClick={onHide}></div>
      <div className={classes.history}>
        <div className={classes.historyTitle}>LỊCH SỬ</div>
        <div className={classes.historyList}>
          {rewardHistory?.map((data, idx) => {
            return renderGift(data, idx);
          })}
        </div>
      </div>
    </div>
  );
}
