import { useState, useContext, useEffect } from "react";
import { ProgressBar, Step } from "react-step-progress-bar";
import "react-step-progress-bar/styles.css";
import StyleLoader from "utils/style-loader";
import { GlobalStore } from "../store";
export const MissionProcess = ({ mission, userProcess }) => {
  const classes = StyleLoader.mission_process_use_style();
  const [showGift, setShowGift] = useState(false);
  const [chosenGift, setChosenGift] = useState(undefined);
  const { showModal } = useContext(GlobalStore.context);
  useEffect(() => {
    if (showModal && showGift) {
      setShowGift(false);
    }
  }, [showModal, showGift]);
  if (!mission) return <></>;

  const maxPoint = mission.at(-1)?.points ?? 0;

  const getPercentage = (userProcess) => {
    const percentageBreak = 100 / mission.length;
    for (let i = 0; i < mission.length; i++) {
      const previousPoint = mission[i - 1]?.points || 0;
      const currentPoint = mission[i].points;
      if (userProcess <= currentPoint && userProcess >= previousPoint) {
        const distance = currentPoint - previousPoint;
        const diff = userProcess - previousPoint;
        const percent =
          (diff / distance / mission.length) * 100 + i * percentageBreak;
        return percent;
      }
    }
  };

  let percent = getPercentage(Math.min(userProcess, maxPoint));

  return (
    <div className={classes.missionProcessContainer}>
      <ProgressBar
        filledBackground="linear-gradient(to right, #fefb72, #f0bb31)"
        percent={percent}
      >
        <Step transition="scale">
          {({ accomplished, index }) => (
            <div
              className={`${classes.transitionStep} ${
                accomplished ? "accomplished" : null
              }`}
            >
              0
            </div>
          )}
        </Step>
        {mission?.map((mission, idx) => {
          return (
            <Step transition="scale" key={idx}>
              {({ accomplished }) => (
                <div
                  title={mission.gift.name}
                  onClick={() => {
                    setShowGift(false);
                    setTimeout(() => {
                      setShowGift(!showGift || chosenGift?.idx !== idx);
                    }, 0);
                    setChosenGift({
                      gift: mission.gift,
                      idx: idx,
                    });
                  }}
                  className={`${classes.transitionStep} ${
                    accomplished ? "accomplished" : null
                  }`}
                >
                  {mission.points}
                </div>
              )}
            </Step>
          );
        })}
      </ProgressBar>
      <div
        className={`RSPBstep ${classes.currentProcess}`}
        style={{
          left: percent + "%",
        }}
      >
        {Math.min(userProcess, 100)}
      </div>

      {showGift ? (
        <div
          className={classes.showGift}
          style={{
            left: ((chosenGift.idx + 1) / mission.length) * 100 + "%",
            transform: "translate(-50%)",
            width: "30%",
          }}
        >
          {chosenGift.gift.name}
        </div>
      ) : null}
    </div>
  );
};
