import axios from "axios";
import Config from "./config";

export const get = async (url, params) => {
  const apiUrl = Config.API_VLTK + "/" + url;
  const zgsk = window.ZGS.getZgsk();
  // const zgsk = "rBYh.120126539.89.COx0oduTn8_IEUo8cSN4ZvFRbC9HsCqLcVlvF_l5n8y";
  const urlSearchParams = {
    ...params,
    zgsk,
  };
  try {
    const res = await axios.get(apiUrl, {
      params: urlSearchParams,
    });

    return res.data;
  } catch (error) {
    throw error;
  }
};

const getAPIDefault = async (api) => {
  const res = await get(api, {
    event: Config.EVENT_SKU,
  });
  return res;
};

export const getLeaderBoard = async () => {
  return getAPIDefault("get-leaderboard");
};

