import { createUseStyles } from "react-jss";
import "./App.css";

import Overlay from "components/Overlay";
import Footer from "scenes/Footer";
import Header from "scenes/Header";
import Section_1 from "scenes/Section_1";
import Section_2 from "scenes/Section_2";
import Section_3 from "scenes/Section_3";
import Section_4 from "scenes/Section_4";
import Section_5 from "scenes/Section_5";
import { preloadAsset } from "utils/asset-loader";
import { login } from "utils/zgs-helper";

import { useEffect, useReducer, useState } from "react";
import { GlobalStore } from "../src/store";

const useStyles = createUseStyles({
  app: {
    maxWidth: 500,
    margin: "auto",
  },
});
function App() {
  const GlobalContext = GlobalStore.context;

  useEffect(() => {
    console.log("initialize_app");
    login();
    preloadAsset();
  }, []);
  const classes = useStyles();
  const [overlay, setOverlay] = useState(false);
  const [{ showModal }, dispatch] = useReducer(
    GlobalStore.reducer,
    GlobalStore.state
  );
  return (
    <div className={classes.app}>
      <GlobalContext.Provider value={{ showModal, dispatch }}>
        <Overlay />
        <Header />
        <Section_1 />
        <Section_2 setOverlay={setOverlay} overlay={overlay} />
        <Section_3 />
        <Section_4 />
        <Section_5 />
        <Footer />
      </GlobalContext.Provider>
    </div>
  );
}

export default App;
