import Asset from "utils/asset-loader";
import Navigate from "utils/navigate-helper";
import StyleLoader from "utils/style-loader";

function Header() {
  const classes = StyleLoader.header_use_style();
  return (
    <div className={classes.header}>
      <img alt="icon" className={classes.icon} src={Asset.header.ICON} />
      <div className={classes.playButton} onClick={Navigate.toPlay}>
        CHƠI NGAY
      </div>
      <div className={classes.homeButton} onClick={Navigate.toHome}></div>
    </div>
  );
}

export default Header;
