import Asset from "utils/asset-loader";
import StyleLoader from "utils/style-loader";

export default function MissionModal({ onHide, missionData }) {
  const classes = StyleLoader.mission_modal_use_style();
  return (
    <div className={classes.missionModal}>
      <div className={classes.topBoard}></div>
      <div className={classes.bottomBoard}></div>

      <div className={classes.btnExit} onClick={onHide}></div>
      <div className={classes.missionTitle}>NHIỆM VỤ</div>
      <div className={classes.mission}>
        {missionData.map((mission, idx) => (
          <div className={classes.missionRow} key={idx}>
            <div className={classes.missionTitleCol}>{mission.text}</div>
            <div className={classes.missionButtonCol}>
              {/* <div className={classes.btnCollect}></div> */}
              <img
                onClick={mission.action}
                src={Asset.section_2.BTN_COLLECT}
                className={classes.btnCollect}
                alt="button-collect"
              />
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
