import Asset from "utils/asset-loader";
import StyleLoader from "utils/style-loader";
import Navigate from "utils/navigate-helper";

function Section_1() {
  const classes = StyleLoader.section_1_use_style();
  return (
    <div className={classes.section_1}>
      <img
        alt="background"
        className={classes.bg}
        src={Asset.section_1.BG_MAIN}
      />
      <img alt="title" className={classes.title} src={Asset.section_1.TITLE} />

      <img
        alt="character"
        className={classes.characterBoy}
        src={Asset.section_1.CHARACTER_BOY}
      />
      <img
        alt="character"
        className={classes.characterGirl}
        src={Asset.section_1.CHARACTER_GIRL}
      />
      <img
        alt="card"
        className={classes.ra_mat_card}
        src={Asset.section_1.RA_MAT_CARD}
      />
      <div className={classes.ra_mat_card_effect}></div>

      <img alt="logo" className={classes.logo} src={Asset.section_1.LOGO} />
      <img
        alt="banner"
        className={classes.banner}
        src={Asset.section_1.BANNER}
      />

      <div className={classes.itemGroup}>
        <a href="#con-lon-xuat-the">
          <img
            alt="item"
            className={classes.item}
            src={Asset.section_1.EVENT_1}
          />
        </a>

        <a href="#vong-quay-bang-huu">
          <img
            alt="item"
            className={classes.item}
            src={Asset.section_1.EVENT_2}
          />
        </a>
        <img
          alt="item"
          className={classes.item}
          src={Asset.section_1.EVENT_3}
        />
        <a href="#vo-lam-di-muon-noi">
          <img
            alt="item"
            className={classes.item}
            src={Asset.section_1.EVENT_4}
          />
        </a>

        <a href="#su-kien-khac">
          <img
            alt="item"
            className={classes.item}
            src={Asset.section_1.EVENT_5}
          />
        </a>
      </div>
      <div className={classes.joinButton} onClick={Navigate.toPlay}></div>
      <img
        alt="decoration"
        className={classes.frontDecor}
        src={Asset.section_1.FRONT_DECOR}
      />
    </div>
  );
}

export default Section_1;
