import { useEffect } from "react";
import { useState } from "react";
import {
  getAvailableMobileNetworksByGift,
  getPhoneCard,
  getRewardHistory,
} from "utils/api-helper";
import AvailableNetworksSelection from "./AvailableNetworksSelection";

import StyleLoader from "utils/style-loader";

const syntax = {
  viettel: "*100*code#",
  mobifone: "*100*code#",
  vinaphone: "*100*code#",
};

const PhoneCardWithoutValue = ({ gift, setRewardHistory }) => {
  const classes = StyleLoader.phone_card_history_use_style();

  const [networks, setNetworks] = useState();

  const [value, setValue] = useState();

  useEffect(() => {
    const getAvailableMobileNetworksByGiftData = async () => {
      if (gift) {
        try {
          const res = await getAvailableMobileNetworksByGift(gift.id);

          setNetworks(res.networks ?? []);
          setValue(res.networks ? res.networks[0] : "");
        } catch (error) {
          console.log(
            "==== ~ getAvailableMobileNetworksByGiftData ~ error",
            error
          );
        }
      }
    };
    getAvailableMobileNetworksByGiftData();
  }, [gift]);

  return (
    networks && (
      <div className={classes.frame}>
        <div>Chọn nhà mạng và nhận ngay thẻ cào</div>
        <AvailableNetworksSelection
          networks={networks}
          onChange={(e) => {
            setValue(e.target.value);
          }}
        />
        <div
          className={classes.btn}
          onClick={async () => {
            console.log(value);

            const res = await getPhoneCard(value, gift.id);
            const rewardHistory = await getRewardHistory();
            console.log(res);
            setRewardHistory(rewardHistory.gifts);
          }}
        >
          Nhận ngay
        </div>
      </div>
    )
  );
};

const PhoneCardWithValue = ({ phoneCardValue }) => {
  const phoneCard = phoneCardValue.split(";");
  const classes = StyleLoader.phone_card_history_use_style();

  return (
    <div className={classes.frame}>
      <div>Nhà mạng: {phoneCard[0]}</div>
      <div>Mã thẻ: {phoneCard[1]}</div>
      <div>Seri: {phoneCard[2]}</div>
      <div>Giá trị: {phoneCard[3]}k</div>
      <div
        className={classes.btn}
        onClick={() => {
          document.location.href = `tel:${syntax[phoneCard[0]].replace(
            "code",
            phoneCard[1]
          )}`;
        }}
      >
        Nạp ngay
      </div>
    </div>
  );
};

export const PhoneCardHistory = ({ gift, setRewardHistory }) => {
  if (gift.value) {
    const values = gift.value.split("|");
    return (
      <>
        {values.map((value, idx) => {
          return <PhoneCardWithValue phoneCardValue={value} key={idx} />;
        })}
      </>
    );
  } else {
    return (
      <PhoneCardWithoutValue gift={gift} setRewardHistory={setRewardHistory} />
    );
  }
};
