export default (state, action) => {
  const { type, payload } = action;
  console.log(action);
  switch (type) {
    case "set-loading": {
      return { ...state, loading: payload };
    }
    case "set-user": {
      return { ...state, user: payload };
    }
    case "set-userData": {
      return { ...state, userData: payload };
    }
    case "set-firstLoad": {
      return { ...state, firstLoad: payload };
    }
    case "set-overlay": {
      return { ...state, showModal: payload };
    }
  }
  return state;
};
