import StyleLoader from "utils/style-loader";

const syntax = {
  viettel: "*100*code#",
  mobifone: "*100*code#",
  vinaphone: "*100*code#",
};
const PhoneCardWithValue = ({ phoneCardValue }) => {
  const phoneCard = phoneCardValue.split(";");
  const classes = StyleLoader.phone_card_receive_modal_use_style();

  return (
    <div className={classes.frame}>
      <div>Nhà mạng: {phoneCard[0]}</div>
      <div>Mã thẻ: {phoneCard[1]}</div>
      <div>Seri: {phoneCard[2]}</div>
      <div>Giá trị: {phoneCard[3]}k</div>
      <div
        className={classes.btn}
        onClick={() => {
          document.location.href = `tel:${syntax[phoneCard[0]].replace(
            "code",
            phoneCard[1]
          )}`;
        }}
      >
        Nạp ngay
      </div>
    </div>
  );
};

export const PhoneCardReceiveModal = ({ card, onHide }) => {
  const phoneCards = card.split("|");
  const classes = StyleLoader.phone_card_receive_modal_use_style();

  return (
    <div className={classes.modal}>
      <div className={classes.btnExit} onClick={onHide}></div>
      <div className={classes.content}>
        {phoneCards.map((value, idx) => {
          return <PhoneCardWithValue phoneCardValue={value} key={idx} />;
        })}
      </div>
    </div>
  );
};
