import Asset from "utils/asset-loader";
import StyleLoader from "utils/style-loader";

export default function Leaderboard({ data }) {
  const classes = StyleLoader.leaderboard_use_style();
  return (
    <div className={classes.leaderboard}>
      <div className={classes.header}>
        <div className={classes.col1}>STT</div>
        <div className={classes.col2}>Nhân Vật</div>
        <div className={classes.col3}>Tổng Số Lượt Quay Đã Có</div>
      </div>
      {data.map((user, idx) => (
        <div className={classes.row} key={idx}>
          <img alt="line" className={classes.line} src={Asset.section_3.LINE} />

          <div className={classes.col1}>{idx + 1}</div>
          <div className={classes.col2}>{user.name || user.userId}</div>
          <div className={classes.col3}>{user.totalTurns}</div>
        </div>
      ))}
    </div>
  );
}
